import { Carousel } from "antd";
import React, { useState } from "react";

const contentStyle = {
  height: "300px",
  color: "#fff",
  lineHeight: "300px",
  textAlign: "center",
  background: "#f5f5f5",
};

const App = () => {
  const [imageList] = useState([
    require(`@Mocks/banner1.png`),
    require(`@Mocks/banner2.png`),
  ]);

  return (
    <>
      <Carousel autoplay>
        {imageList.map((x, i) => {
          return (
            <div key={i}>
              <h3 style={contentStyle}>
                <div
                  style={{
                    height: "100%",
                    width: "100%",
                    backgroundImage: `url('${x}')`,
                    backgroundPosition: "top",
                    backgroundSize: "cover",
                    backgroundRepeat: "no-repeat",
                  }}
                />
              </h3>
            </div>
          );
        })}
      </Carousel>
    </>
  );
};

export default App;
